}
.elementor-widget-foursquare-hero-banner .foursquare-hero-banner .foursquare-hero-banner-content {
  width: 50%;
  padding-bottom: 20px;
  display: flex;
  flex-direction: column;
  flex: 1 1 100%;
}
.elementor-widget-foursquare-hero-banner .foursquare-hero-banner .foursquare-hero-banner-content .foursquare-hero-banner-btn-group {
  margin-top: 30px;
}
.elementor-widget-foursquare-hero-banner .foursquare-hero-banner .foursquare-hero-banner-content .foursquare-hero-banner-content__heading h1, .elementor-widget-foursquare-hero-banner .foursquare-hero-banner .foursquare-hero-banner-content .foursquare-hero-banner-content__heading h2, .elementor-widget-foursquare-hero-banner .foursquare-hero-banner .foursquare-hero-banner-content .foursquare-hero-banner-content__heading h3, .elementor-widget-foursquare-hero-banner .foursquare-hero-banner .foursquare-hero-banner-content .foursquare-hero-banner-content__heading h4, .elementor-widget-foursquare-hero-banner .foursquare-hero-banner .foursquare-hero-banner-content .foursquare-hero-banner-content__heading h5, .elementor-widget-foursquare-hero-banner .foursquare-hero-banner .foursquare-hero-banner-content .foursquare-hero-banner-content__heading h6 {
  font-family: inherit !important;
}
.elementor-widget-foursquare-hero-banner .foursquare-hero-banner .foursquare-hero-banner-image {
  width: 50%;
  flex: 1 1 100%;
}

/* Slick Slider Arrows */
ul.slick-dots {
  flex-wrap: wrap;
}

.slick-arrow {
  width: 40px !important;
  height: 40px !important;
  font-size: 0 !important;
  background: rgba(0, 0, 0, 0) !important;
  cursor: pointer !important;
  top: 40%;
  transform: translateY(-50%);
  border: none !important;
  position: absolute;
  display: block;
}

.slick-arrow:before {
  width: 16px;
  height: 16px;
  content: "";
  display: block;
  position: absolute;
  top: 11px;
}

.slick-prev {
  z-index: 1;
}

.slick-prev:before {
  border-top: 3px solid #666;
  border-left: 3px solid #666;
  transform: rotate(-45deg);
}

.slick-prev[aria-disabled=true] {
  opacity: 0.4;
  pointer-events: none;
}

.slick-next {
  right: 20px;
}

.slick-next:before {
  border-top: 3px solid #506cf0;
  border-left: 3px solid #506cf0;
  transform: rotate(135deg);
}

.slick-next[aria-disabled=true] {
  opacity: 0.4;
  pointer-events: none;
}

/* Slick Slider Dots */
.slick-dots {
  text-align: center; /* Center align dots */
  margin-top: 20px; /* Space above dots */
}

.slick-dots li {
  display: inline-block;
  margin: 0 5px; /* Space between dots */
}

.slick-dots li button {
  font-size: 0; /* Hide default button text */
  background-color: #dce1fc; /* Dot color */
  width: 12px; /* Dot width */
  height: 12px; /* Dot height */
  border-radius: 50%; /* Rounded dots */
  border-color: #506cf0; /* Remove dot border */
  padding: 5px;
}

.slick-dots li.slick-active button {
  background-color: #506cf0; /* Active dot color */
}

.slick-dots li button:hover {
  background-color: #506cf0; /* Dot color on hover */
}

.elementor-widget-foursquare-news {
  border-radius: 20px;
  overflow: hidden;
}
.elementor-widget-foursquare-news .foursquare-news .foursquare-news-heading {
  text-align: center;
  padding: 20px 0;
  background-color: #f6f6f6;
}
.elementor-widget-foursquare-news .foursquare-news .foursquare-news-slide__inner {
  padding: 20px 0 50px;
  padding-right: 50px;
  background-color: #f6f6f6;
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.elementor-widget-foursquare-news .foursquare-news .foursquare-news-slide__inner .foursquare-news-link {
