}
.elementor-widget-foursquare-news .foursquare-news .foursquare-news-slide__inner .foursquare-news-link a {
  color: inherit;
  text-decoration: none;
}
.elementor-widget-foursquare-news .foursquare-news .slick-list::after {
  right: 0;
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  width: 135px;
  background: 0 0;
  background-image: linear-gradient(to left, #f6f6f6 0%, rgba(255, 255, 255, 0) 100%);
}

.elementor-widget-foursquare_logo_slider .foursquare-logo-slider {
