}
.elementor-widget-foursquare-media-text .foursquare-media-text {
  display: flex;
  flex-direction: row-reverse;
  align-items: center;
  position: relative;
}
.elementor-widget-foursquare-media-text .foursquare-media-text .foursquare-media-text-content {
  display: flex;
  align-items: center;
  width: 50%;
  height: 100%;
  flex-wrap: wrap;
}
.elementor-widget-foursquare-media-text .foursquare-media-text .foursquare-media-text-content__inner {
  position: relative;
  margin-top: 40px;
  padding-bottom: 40px;
}
.elementor-widget-foursquare-media-text .foursquare-media-text .foursquare-media-text-content__inner .fsq-anim-vbar {
  width: 3px;
  height: 0;
  background-color: #33f;
  position: absolute;
  left: -32px;
  top: 0;
}
.elementor-widget-foursquare-media-text .foursquare-media-text .foursquare-media-text-content__inner .fsq-anim-hbar {
  width: 0;
  height: 3px;
  background-color: #33f;
  position: absolute;
  left: -32px;
  bottom: 0;
  max-width: 60vw;
}
.elementor-widget-foursquare-media-text .foursquare-media-text .foursquare-media-text-content__inner .fsq-anim-h-rtl {
  -webkit-transform: translateX(-100%);
  transform: translateX(-100%);
}
.elementor-widget-foursquare-media-text .foursquare-media-text .foursquare-media-text-content__inner .fsq-anim-vbar.vbar-1 {
  top: 5px;
  animation: MTV2VBar 0.8s linear 0.7s forwards;
}
.elementor-widget-foursquare-media-text .foursquare-media-text .foursquare-media-text-content__inner .fsq-anim-hbar.hbar-1 {
  bottom: -5px;
  animation: MTV2HBar 2.5s linear 1.5s forwards;
}
.elementor-widget-foursquare-media-text .foursquare-media-text .foursquare-media-text-content__inner .foursquare-media-text-heading_title {
  position: relative;
}
.elementor-widget-foursquare-media-text .foursquare-media-text .foursquare-media-text-content__inner .foursquare-media-text-heading_title .fsq-anim-title-bullet {
  position: absolute;
  top: 0;
  left: -50px;
}
.elementor-widget-foursquare-media-text .foursquare-media-text .foursquare-media-text-content__inner .foursquare-media-text-description {
  margin-bottom: 30px;
  margin-top: 20px;
}
.elementor-widget-foursquare-media-text .foursquare-media-text .foursquare-media-text-image {
  padding-right: 5%;
  width: 50%;
}
.elementor-widget-foursquare-media-text .foursquare-media-text .foursquare-media-text-image img {
  max-width: 100%;
  display: block;
  position: relative;
  z-index: 1;
  margin: 0 auto;
  height: auto;
}

@keyframes MTV2VBar {
  0% {
    height: 0;
  }
  to {
    height: 100%;
  }
}
@keyframes MTV2HBar {
  0% {
    width: 0;
  }
  to {
    width: 100vw;
  }
}
